.education {
  padding: 0 32px 96px;
}

.content {
  margin: 24px 0;
  display: grid;
  grid-gap: 64px;
  grid-template:
    "area1" auto
    "area2" auto
    "area3" auto
    "area4" auto
    "area5" auto / minmax(0, auto);
  overflow: hidden;
  & > * {
    $areas: 1, 2, 3, 4, 5;
    @each $area in $areas {
      &:nth-child(#{$area}) {
        grid-area: area#{$area};
      }
    }
  }
}

%title {
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
}

.step {
  z-index: 1;
  &Number {
    margin-bottom: 16px;
    font-size: 24px;
    color: #cece84;
  }
  &Images {
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    height: 60px;
  }
  &Image {
    max-width: 30%;
    max-height: 60px;
    &:not(:last-child) {
      margin-right: 5%;
    }
  }
  &Title {
    @extend %title;
    color: #cece84;
  }
}

.birdImage {
  &Wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px;
  }
  margin-top: -80px;
  height: 640px;
}

.title {
  @extend %title;
  color: #efe9ea;
}

.text {
  line-height: 1.4;
  font-size: 14px;
  color: rgba($color: #efe9ea, $alpha: 0.75);
}

.button {
  margin: 24px auto 0;
  display: block;
  width: 100%;
  max-width: 384px;
  height: 40px;
  font-size: 14px;
  font-weight: 500;
  color: #41b167;
  border: 1px solid #41b167;
}

.footerLink {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 96px;
  font-size: 16px;
  font-weight: 700;
  text-decoration: none !important;
  color: #20263a;
  background-color: #41b167;
  box-shadow: 0 0 4px #41b167;
  z-index: 1;
}

@media (min-width: 768px) {
  .content {
    grid-template:
      "area1 area5" auto
      "area2 area5" auto
      "area3 area5" auto
      "area4 area5" auto / minmax(0, 1fr) minmax(0, 1fr);
  }
  .step {
    &Images {
      height: 112px;
    }
    &Image {
      max-height: 112px;
    }
  }
}

@media (min-width: 1200px) {
  .content {
    grid-template:
      "area1 area2 area5" auto
      "area3 area4 area5" auto / minmax(0, 1fr) minmax(0, 1fr) minmax(0, 1fr);
  }
}
